import React, { Component } from 'react';
import styled from 'styled-components';
import { connect } from "react-redux";
import { setCategory} from "../../actions/iconActions";
import { bindActionCreators } from "redux";
import { BREAKPOINT_LG,BREAKPOINT_MD,GREEN_COLOR,FONT_COLOR,LIGHT_BACKGROUND,BLUE_COLOR,DARK_BORDER_COLOR, DARK_FONT_COLOR, BORDER_COLOR_DARK} from '../types';
import data from '../../reducers/data/categories.json'

const CategoryList = styled.ul`
    padding: 15px;
    list-style: none;
    margin: 0px;
     @media screen and (min-width: ${BREAKPOINT_LG}){
        padding:20px; 
        padding-left:0px;padding-top: 20px;
        
        border-right: 1px solid  ${DARK_BORDER_COLOR};
 
        }
  

    li{
        font-size: 14px;
        
        
        padding: 0px;
        cursor: pointer;
        display: inline-block;
        
        width: 20%;
        
        @media screen and (max-width: ${BREAKPOINT_LG}) {
            width: 33.33%;
        }
        @media screen and (max-width: ${BREAKPOINT_MD}) {
            width: 50%;
        }
        @media screen and (min-width: ${BREAKPOINT_LG}){
        width: 100%;
    display:block;}
        div{
        border-radius: 2px;
        padding: 3px 15px;
        border: 2px solid  transparent;
        transition:0.1s;
        -webkit-touch-callout: none;
          -webkit-user-select: none;
          -khtml-user-select: none;
          -moz-user-select: none;
          -ms-user-select: none;
          user-select: none;
           @media screen and (min-width: ${BREAKPOINT_LG}){
        min-width: 160px;}
            display: block;
            color: ${ FONT_COLOR};
            
           &:hover{
           
            
        } 
        }
        
        &.active{
            
            
            div{
                  color:#b06c4a;
              
            }
            
        }
    }
`;
class Categories extends Component {
constructor(props) {
        super(props);
       this.state= {
categories: data
       }
        this.handleClick = this.handleClick.bind(this);
      

    }
    
    handleClick(name){
        var el = document.querySelector(".icons-right-section");
        var boundingRect = el.getBoundingClientRect();
         window.scroll({
    top: (boundingRect.top+window.pageYOffset)-128,  
    behavior: 'smooth' 
  });
 
        this.props.setCategory(name,this.props.style,this.props.search);
    }
render(){
    const categoryItems = this.state.categories.map((category,num) => {

        return (<li key={num} className={this.props.search===category.name ? 'active':''} onClick={()=>this.handleClick(category.name)}><div>{category.name}</div></li>
        );
    });

    return (

        <div className="fadeIn animated" id="filter">
         <CategoryList>
            {categoryItems}
         </CategoryList>   
        </div>
    );
}
};

function mapDispatchToProps(dispatch) {
    return bindActionCreators(
        {
            setCategory,
        },
        dispatch
    );
}

export default connect(
    null,
    mapDispatchToProps
)(Categories);